:root {
  --sunglow: #FFCC33;
  --tulip: #E6B82E;
  --lilac: #9d42b9;
  --dodger: #41ADFF;
  --jellybean: #2874ad;
  --persian-red: #d23430;
  --flame: #E34F26;
  --black-pearl: #1d252c;
  --bunker: #171d23;
  --caribbean-green: #00C58E;
  --deep-sea: #108775;
  --buttery-white: #eee8d5;
  --old-lace: #FDF6E3;
  --dt-body-bg: var(--bunker);
  --dt-body-bg-rgb: 23, 29, 35;
  --dt-content-bg: var(--black-pearl);
  --dt-content-bg-rgb: 29, 37, 44;
  --dt-title-color: #ffffff;
  --dt-content-color: #ced8de;
  --dt-link-color:  var(--sunglow);
  --dt-link-hover: var(--tulip);
  --lt-body-bg: var(--buttery-white);
  --lt-body-bg-rgb: 238, 232, 213;
  --lt-content-bg: var(--old-lace);
  --lt-content-bg-rgb: 253, 246, 227;
  --lt-title-color: #111111;
  --lt-content-color: #4e5e64;
  --lt-link-color:  var(--jellybean);
  --lt-link-hover: var(--dodger); }

