/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
  color: var(--content-color);
  font-family: 'Raleway', sans-serif;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.5; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Raleway', sans-serif;
  font-weight: 400; }

p {
  margin-top: 0;
  margin-bottom: 1.5em; }

dfn, cite, em, i {
