  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button, a.btn,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid;
  border-color: var(--link-color);
  border-radius: 0;
  background-color: var(--body-bg);
  color: var(--link-color);
  font-size: 1rem;
  line-height: 1;
  padding: 0.5rem 1.25rem;
  font-weight: 200;
  text-decoration: none;
  transition: color .3s,background-color .3s; }
  button:hover, a.btn:hover,
  input[type="button"]:hover,
  input[type="reset"]:hover,
  input[type="submit"]:hover {
    color: var(--body-bg);
    background: var(--link-color); }

button:active, button:focus,
a.btn:active, a.btn:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
  border-color: var(--link-hover);
  color: var(--link-hover); }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  color: var(--content-color);
  background-color: var(--body-bg);
  border: 1px solid var(--link-color);
  border-radius: 0;
  padding: 0.25rem; }

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
  color: var(--content-color); }

select {
  border: 1px solid var(--link-color); }

textarea {
  width: 100%; }

.theme-toggle-wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 5px;
  font-weight: 600;
  color: var(--link-color);
  transition: color .6s; }
  .theme-toggle-wrapper .toggle-icon {
    width: 14px;
    height: 14px; }
    .theme-toggle-wrapper .toggle-icon path {
      fill: var(--link-color);
      transition: fill .6s; }
  .theme-toggle-wrapper .toggle-switch {
    width: 48px;
    height: 20px;
    margin: 0 4px; }
    .theme-toggle-wrapper .toggle-switch label.switch {
      width: 48px;
      height: 20px;
      position: relative;
      display: inline-block; }
      .theme-toggle-wrapper .toggle-switch label.switch:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 48px;
        height: 20px;
        border-radius: 20px;
        border: 1px solid var(--link-color);
        transition: border-color .6s;
        background-color: transparent; }
      .theme-toggle-wrapper .toggle-switch label.switch:after {
        content: '';
        display: block;
        position: absolute;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: var(--link-color);
        top: 2px;
        left: 2px;
        transform: translateX(0);
        transition: transform 0.3s ease, background-color .6s; }
    .theme-toggle-wrapper .toggle-switch input[type=checkbox] {
      display: none; }

[data-theme=dark] .theme-toggle-wrapper .toggle-switch label.switch:after {
  transform: translateX(28px); }

.wave-bg {
