.logo-wrapper {
  width: 100px;
  height: 100px;
  position: relative;
  margin: 20px auto 0; }
  .logo-wrapper.main {
    max-width: 300px;
    width: 50vw;
    max-height: 300px;
    height: 50vw;
    margin: 60px auto 20px; }
    .logo-wrapper.main #dl_logo_animate {
