      width: 50vw;
      max-height: 300px;
      height: 50vw; }

#dl_logo_animate {
  width: 100px;
  height: 100px;
  display: block;
  position: absolute;
  z-index: 10; }
  #dl_logo_animate .st1 {
    fill: none;
    stroke: var(--sunglow);
